@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    background: linear-gradient(0deg, #0D324D 0%, #7F5A83 100%);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 100%;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: Lato;
    font-size: 12px;
}

.wrapper {
    position: relative;
    margin-top: 150px;
    margin-bottom: 50px;
    width: 330px;
    background-color: #fafbfc;
    border-radius: 10px;
}

.form__input {
    display: flex;
    justify-content: center;
}

.form__btn {
    display: flex;
    justify-content: center;
}

.form__input--txt {
    width: 300px;
    margin-top: 105px;
    margin-bottom: 15px;
    font: Lato;
    font-size: 1.05rem;
    font-weight: 700;
    color: #2e2e2e;
    background-color: #dce3e4;
    height: 48px;
    border: 0px;
    border-radius: 5px;
    outline: none;
    padding-left: 10px;
    padding-right: 10px;
}

.form__input--btn {
    width: 300px;
    font-size: 1.05rem;
    font-weight: 700;
    color: #f9f6f6;
    background-color: #0d324dde;
    height: 48px;
    border: 0px;
    border-radius: 5px;
    outline: none;
    transition: all 0.5s;
}

.form__input--btn:hover {
    background-color: #1f0b43b6;
    cursor: pointer;
}

.list {
    margin: 15px;
}

.list__ul {
    margin-top: 30px;
    list-style: none;
    font-size: 1.05rem;
    text-align: center;
}

li {
    margin-top: 10px;
    margin-bottom: 10px;
}

.username {
    margin-top: 15px;
    text-align: center;
    font-size: 2rem;
    font-weight: 900;
    color: #0D8092;
    margin-bottom: 10px;
    transition: 0.7s;
}

.username:visited {
    color: #0D8092;
}

.username:hover {
    color: #074954;
}

.logo {
    display: flex;
    justify-content: center;
    position: relative;
    background-color: violet;
    margin-bottom: 10px;
}

.logo--img {
    position: absolute;
    top: -100px;
    width: 200px;
    background-color: #fafbfc;
    border-radius: 50%;
}

.avatar {
    display: flex;
    justify-content: center;
}

.avatar--img {
    width: 90%;
    height: 90%;
    margin-top: 10px;
    border-radius: 5px;
}
